import { useMemo } from 'react';
import { useSelector } from 'react-redux';
import { selectClientId, selectCompanyId } from '../app/auth/authSlice';
export const getClientProps = () => {
    const client = useSelector(selectClientId);
    const company = useSelector(selectCompanyId);
    return useMemo(() => ({
        client_id: parseInt(client),
        company_id: company,
        company: company,
    }), [client, company]);
};
