import { MenuItem } from "primereact/menuitem"
import { templateFn } from '@igblsln/control';

const moduleItems: any = {
    projects: 'Projects',
    vendor: 'Vendors',
    contract: 'Contractors',
    purchase: 'Purchases',
    inventory: 'Material Item',
    // inventory: 'Inventory',
    payment: 'Payments',
    estimation: 'Estimations',
    // customer: 'Customers',
    sales: 'Sales',
    inquiry: 'Inquiry',
    users: 'Users',
    clients: 'Clients',
    // reports: 'Reports',
    // settings: 'Settings'
}

const modules: MenuItem[] = Object.keys(moduleItems).map(x => ({
    label: moduleItems[x],
    icon: 'p-menuitem-icon-no-margin',
    template: templateFn(`/${x}`),
}))

export default modules